.header-nav {
  --line-height: 20px;
  --height: var(--header-fixed-height);
  color: #fff;
  font-size: 14px;
  position: sticky;
  z-index: 1000;
  top: 0; }
  .header-nav__wrapper {
    background-color: var(--color-main);
    position: relative;
    z-index: 2;
    height: var(--height); }
  .header-nav__list {
    display: flex; }
  .header-nav__item {
    --sub-nav-opacity: 0;
    --sub-nav-visibility: hidden;
    --link-bg: transparent;
    flex: 1 1 auto;
    position: relative; }
    .header-nav__item:not(:first-child) {
      margin-left: -1px; }
    .header-nav__item::before, .header-nav__item::after {
      content: '';
      display: block;
      position: absolute;
      top: calc((var(--height) - var(--line-height)) / 2);
      height: var(--line-height);
      width: 1px;
      background-color: var(--color-main-medium); }
    .header-nav__item::before {
