.header-search-toggle {
  --icon-size: 18px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  height: var(--height);
  text-transform: uppercase;
  transition: background-color var(--trs-1); }
  .header-search-toggle__icon {
    width: var(--icon-size);
    height: var(--icon-size);
    position: relative; }
  .header-search-toggle .icon {
    width: 100%;
    height: 100%;
    transition: opacity var(--trs-1); }
    .header-search-toggle .icon:last-child {
      position: absolute;
      opacity: 0;
      top: 0;
      left: 0; }
  .header-search-toggle[data-touch], .header-search-toggle[data-hover], .header-search-toggle.active {
    background-color: var(--color-main-medium); }
  .header-search-toggle.active .icon:first-child {
    opacity: 0; }
  .header-search-toggle.active .icon:last-child {
    opacity: 1; }
  @media (max-width: 1279.98px) {
    .header-search-toggle {
      padding: 0 16px;
      background-color: var(--color-main-medium); }
      .header-search-toggle[data-touch], .header-search-toggle[data-hover] {
        background-color: var(--color-main-alternate); } }
  @media (max-width: 1279.98px) {
    .header-search-toggle {
      gap: 10px;
      --icon-size: 20px; } }
  @media (max-width: 575.98px) {
    .header-search-toggle {
      padding: 0 var(--container-padding-x);
      background-color: transparent; }
      .header-search-toggle[data-touch], .header-search-toggle[data-hover] {
        background-color: var(--color-main-medium); } }

@media (min-width: 1280px) {
  .mobile-nav-trigger {
    display: none; } }

@media (max-width: 1279.98px) {
  .mobile-nav-trigger {
    --icon-size: 28px;
    width: var(--height);
    height: var(--height);
    padding: calc((var(--height) - var(--icon-size)) / 2);
    background-color: var(--color-main-medium);
    transition: background-color var(--trs-1); }
    .mobile-nav-trigger .icon {
      width: calc(100% + 8px);
      height: calc(100% + 8px);
      margin: -4px; }
    .mobile-nav-trigger[data-touch], .mobile-nav-trigger[data-hover] {
      background-color: var(--color-main-alternate); } }

@media (max-width: 575.98px) {
  .mobile-nav-trigger {
    background-color: transparent; }
    .mobile-nav-trigger[data-touch], .mobile-nav-trigger[data-hover] {
      background-color: var(--color-main-medium); } }

.header-search {
  --icon-size: 18px;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background-color: var(--color-main-medium);
  transform: translateY(-100%);
  transition: transform var(--trs-1); }
  .header-search.active {
    transform: translateY(0); }
  .header-search__form {
    transform: translateY(var(--form-translate));
    display: flex;
    transition: transform var(--trs-1); }
  .header-search__submit {
    flex: 0 0 auto;
    display: flex;
    width: var(--height);
    height: var(--height);
    transition: color var(--trs-1); }
    .header-search__submit .icon {
      width: var(--icon-size);
      height: var(--icon-size);
      margin: auto; }
    .header-search__submit[data-touch], .header-search__submit[data-hover] {
      color: var(--color-main-alternate); }
  .header-search__input {
    display: block;
    width: 100%;
    height: var(--height);
    line-height: var(--height);
    color: #fff; }
    .header-search__input::placeholder {
      color: #fff;
      transition: opacity var(--trs-1);
      text-transform: uppercase; }
    .header-search__input:focus::placeholder {
      opacity: 0; }
  @media (max-width: 1023.98px) {
    .header-search {
      --icon-size: 22px; } }
  @media (max-width: 575.98px) {
    .header-search__container {
      padding-left: 0; } }

.header-lang__list {
  display: flex; }

.header-lang__item:not(:last-child)::after {
  content: '|';
  display: inline-block;
  margin: 0 5px; }

.header-lang__link {
  text-transform: uppercase;
  transition: color var(--trs-1); }
  .header-lang__link[data-touch], .header-lang__link[data-hover] {
    color: var(--color-main-alternate); }
  .header-lang__link.active {
    font-weight: 700;
    color: var(--color-main-alternate);
    cursor: default; }

@media (min-width: 1280px) {
  .mobile-nav {
    display: none !important; } }

@media (max-width: 1279.98px) {
  .mobile-nav {
    line-height: var(--normal-line-height);
    display: none;
    position: fixed;
    z-index: 2000;
    width: 100%;
    height: 100%; }
    .mobile-nav__overlay {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: var(--color-overlay); }
    .mobile-nav__container {
      height: 100%;
      overflow-y: auto;
      overflow-x: hidden;
      position: relative;
      z-index: 2;
      background-color: var(--color-main);
      color: #fff;
      max-width: 360px; }
    .mobile-nav__close {
      --icon-size: 28px;
      width: var(--header-fixed-height);
      height: var(--header-fixed-height);
      padding: calc((var(--header-fixed-height) - var(--icon-size)) / 2);
      background-color: var(--color-main-medium);
      position: sticky;
      z-index: 2;
      top: 0;
      margin: 0 0 0 auto;
      transition: background-color var(--trs-1); }
      .mobile-nav__close .icon {
        width: 100%;
        height: 100%; }
      .mobile-nav__close[data-touch], .mobile-nav__close[data-hover] {
        background-color: var(--color-main-alternate); }
    .mobile-nav__list {
      --item-py: 12px;
      --px: 20px;
      padding-bottom: var(--header-fixed-height); }
    .mobile-nav__link {
      display: block;
      padding: var(--item-py) var(--px);
      transition: background-color var(--trs-1);
      font-size: 18px; }
      .mobile-nav__link[data-touch], .mobile-nav__link[data-hover] {
        background-color: var(--color-main-medium); }
      .mobile-nav__link.active {
        cursor: default;
        background-color: var(--color-main-alternate); }
    .mobile-nav__sub-list {
      --item-py: 6px;
      --py: 10px;
      --px: 20px;
      padding: 10px var(--px);
      position: relative; }
      .mobile-nav__sub-list::before {
        content: '';
        display: block;
        width: 2px;
        height: calc(100% - var(--py) * 2 - var(--item-py) * 2);
        top: calc(var(--py) + var(--item-py));
        left: var(--px);
        position: absolute;
        background-color: var(--color-main-medium); }
    .mobile-nav__sub-link {
      padding: var(--item-py) 0 var(--item-py) var(--px);
      display: block;
      transition: color var(--trs-1); }
      .mobile-nav__sub-link[data-touch], .mobile-nav__sub-link[data-hover] {
        color: var(--color-main-alternate); }
      .mobile-nav__sub-link.active {
        cursor: default;
        background-color: var(--color-main-alternate); } }

@media (max-width: 575.98px) {
  .mobile-nav__container {
    max-width: 300px; }
  .mobile-nav__sub-list {
    --px: var(--container-padding-x); }
  .mobile-nav__list {
    --px: var(--container-padding-x); }
  .mobile-nav__close {
    background-color: transparent; }
    .mobile-nav__close[data-touch], .mobile-nav__close[data-hover] {
      background-color: var(--color-main-medium); } }

/*========== Footer ==========*/
