.footer {
  background-color: var(--color-main);
  color: #fff;
  padding: 30px 0;
  line-height: 1.5;
  font-size: 14px;
  overflow: hidden;
  &__container {
    display: flex;
    align-items: center;
    gap: 30px;
  }
  &__iso {
    height: 50px;
    width: auto;
    flex: 0 0 auto;
  }
  &__logo {
    height: 50px;
    flex: 0 0 auto;
  }
  &__text {
    color: var(--color-light);
    flex: 1 1 auto;
  }
  &__socials {
    flex: 0 0 auto;
    --size: 34px;
    --gap: 10px;
    --color: #fff;
    --color-hover: rgba(255, 255, 255, 0.8);
  }

  @include media-breakpoint-down(lg) {
    &__container {
      flex-wrap: wrap;
      justify-content: space-between;
    }
    &__text {
      flex: 1 0 100%;
      order: 4;
    }
  }
}